*{
  padding:0;
  margin:0;
  box-sizing:border-box;
}
.contain{
  width:100vw;
  height:100vh;
  display: grid;
  place-items:center;
  position:relative;
}

.card{
  position: absolute;
  width:90%;
  height:90%;
  scroll-snap-align:center;
  scroll-snap-type:both mandatory;
  display:flex;
  flex-direction:row;
  flex-wrap:wrap;
  justify-content: space-around;
  overflow-x:hidden;
  overflow-y:scroll;

}
.containInfo{
  width:30%;
  height: 55%;
  scroll-snap-align:center;
  scroll-snap-type:both mandatory;
  position:relative;
  margin:2vmin;
  overflow: hidden;
}
.containInfo >img{
  width:100%;
  height:100%;
  position:absolute;
  transform: scale(1.4);
  transition:all 200ms linear;
}

.info{
  width:100%;
  height:50%;
  background:rgba(26, 25, 25, 0.767);
  color:rgb(243, 238, 238);
  position:absolute;
  padding:2vmin;
  overflow: hidden;
  bottom:0%;
  transform:translateY(150%);
  opacity: 0;transition:all 200ms linear;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter:blur(10px);
}

.title{
  position:absolute;
  font-size:3vmin;
  font-weight: bold;
  text-transform: uppercase;
}
.price{
  font-weight: 500;
  font-size:4vmin;
  position:absolute;
  right:5%;
  bottom:8%;
  text-decoration:underline  wavy;
}
.desc{
  width:50%;
  bottom:15%;
  position:absolute;
  left:5%;
  height:50%;
  font-weight: 500;
  transform:translateY(-30%);
  transition:all 400ms linear;
 
  opacity: 0;
  
}
.desc:nth-child(even){
  transform:translateX(-30%);
  transition:all 400ms linear;
 
  opacity: 0;
}
.containInfo:hover .info{
  transform:translateY(0%);
  opacity: 10;
}
.containInfo:hover img{
  transform:scale(1);
  filter:brightness(50%)
}
.containInfo:hover .desc{
  opacity: 10;
  transform: translate(0%);
}
#select{
  width:15vmin;
 position: fixed;
 top:0%;right:0%;
 z-index: 5;
  height:5vmin;
  text-align: center;
  font-weight: bold;
}
#select >option{
  background:rgb(255, 174, 0);
  color:black;
  width:100%;
  height:5vmin;
  padding:5vmin

}
@media only screen and (max-width:550px){
  .containInfo{
    width:100%;height:50%;
  }
  .desc{
    font-size:3vmin;
  }
  .title{
    font-size: 4vmin;
    left:5%;
  }
  #select{
    width:25vmin;
   position: fixed;
   top:0%;right:0%;
   z-index: 5;
    height:5vmin;
    text-align: center;
    font-weight: bold;
  }
}